.canvas-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	position: relative;
}

#canvas {
	border: 1px solid black;
	margin: 10px;
}

.string-container {
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	pointer-events: none;
	font-size: 36px;
	background-color: yellow;
}

.canvas-background-image {
	position: absolute;
	width: 300px;
	height: 300px;
	pointer-events: none;
}

.image-hide {
	visibility: hidden;
}





